import { LoadingProvider } from './../../providers/loading/loading';
import { Component, Input } from '@angular/core';
import { IonicPage, NavController, AlertController } from 'ionic-angular';
import { GlobalProvider } from '../../providers/global/global';
import { RestProvider } from '../../providers/rest/rest';

/**
 * Generated class for the PasswordSendmailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-password-sendmail',
  templateUrl: 'password-sendmail.html',
})
export class PasswordSendmailPage {
  @Input() events: any;

  public email: string;

  public errorEmailEmpty: boolean;
  public errorEmailInvalid: boolean;

  constructor(
    public navCtrl: NavController, 
    public loading: LoadingProvider,
    public global: GlobalProvider, 
    public alertCtrl: AlertController,
    public rest: RestProvider
    ) {
      this.errorEmailEmpty= false;
      this.errorEmailInvalid = false;

      this.events =  {
        "onSendmail" : this.onSendmail
      }
  }

  onEvent = (event: string): void => {
    if (event == "onSendmail" && !this.validate()) {
        return ;
    }
    if (this.events[event]) {
        this.events[event]({
            'email': this.email,
        });
    }        
  }

  validate():boolean {
    this.errorEmailEmpty= false;
    this.errorEmailInvalid = false;
    let re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

    if (!this.email ||this.email.length == 0) {
        this.errorEmailEmpty = true;
    }
    else if (!re.test(String(this.email).toLowerCase())) {
      this.errorEmailInvalid = true;
    }
    
    return !this.errorEmailEmpty && !this.errorEmailInvalid;
  }

  // login and go to home page
  onSendmail = (params): void => {
    this.loading.show();
    this.rest.sendRequestNewPassword(this.email).then(
      (scs:any) => {
        this.navCtrl.push('PasswordCodePage', {token: scs.token});
      },
      (err) => {
        this.loading.hide();
        
        let message = "Không thể kết nối tới máy chủ!";
        if (err.error.error == 'email_not_found'){
          message = "Không tìm thấy tài khoản với email đã nhập!"
        }
        let alert = this.alertCtrl.create({
          title: 'Lỗi',
          message: message,
          buttons: [
            {
              text: 'OK',
              role: 'ok',
            }
          ]
        });
        alert.present();
      }
    )
    
  }

  openLoginPage() {
    this.navCtrl.setRoot('LoginPage');
  }

}



